          "Results show that teams found the tool-based approach valuable, enabling the teams to engage in discussions they were not always capable of having, to voice their concerns, and to challenge the status quo. Teams reported that allocating the time to have these discussions served as a useful prompt to bring out subjects that were harder to have surface during their daily work. Additionally, all participating teams reported that they wanted to adopt one or more of the tools they evaluated into their practice for continued use, indicating that using the proposed tools for working with psychological safety could fit into the practice of agile software teams.",
